[mol_grider] {
	display: block;
	flex: 0 1 auto;
	margin: auto;
}

[mol_grider_tabler] {
	border-spacing: 0;
	box-shadow: 0 0 0 1px var(--mol_skin_light_line);
	transform: translateZ(0);
}

[mol_grider_tabler] > * {
	display: table-row !important;
	transition: none;
}

[mol_grider_tabler] > * > * {
	display: table-cell !important;
}

[mol_grider_tabler] > * > * {
	padding: 0 1rem;
	white-space: nowrap;
	vertical-align: middle;
}

[mol_grider_rower]:not(:last-child) > * {
	border-bottom: 1px solid color( var(--mol_skin_light_line) lightness(+8%) );
}

[mol_grider_tabler] > * > *:not(:last-child)  {
	border-right: 1px solid color( var(--mol_skin_light_line) lightness(+8%) );
}

[mol_grider_tabler] > [mol_grider_header] > * {
	background: var(--mol_skin_light);
	border-bottom: 1px solid var(--mol_skin_light_line);
}

[mol_grider_number] {
	text-align: right;
}

[mol_grider_cellerDimmer] {
	display: inline-block;
	vertical-align: inherit;
}
